@use '../bses/variables' as v;
@use '../bses/mixins' as m;


.nuestro-menu{
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, v.$primario, v.$primario);
    background-repeat: no-repeat;
    background-position: center 5rem, center 95%, 100%;
    padding: 15rem 2rem 2rem 5rem;

    @include m.tablet { 
        padding: 15rem 0 20rem 0;
        background-size: 100%;
    }

    @include m.desktop { 
        background-size: 50%, 50%, 100%;
    }
}

.grid-menu{
    @include m.tablet { 
        @include m.grid(2, 2rem);
    }
}

.menu{
    .h3{

    }
    li{

        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            color: v.$blanco;
            font-size: 2.2rem;
            line-height: 1.5;
            margin: 0 0 3rem 0;
            span{
                display: block;
                color: v.$secundario;
            }

        }
        .precio{
            font-size: 4rem;
            font-weight: 700;
        }
    }
}
